@import 'assets/styles/mixins';

/////////////////////////////////////////////////////////////////////////////////////////
/* SELECTBOXES */

select {
  appearance: none;

  option:disabled {
    opacity: 0.4;
  }
}

select.form-control {
  color: $text;
  height: auto;
  background: #fff center right no-repeat
    url('data:image/png;base64,');
}

select.form-control[multiple] {
  background: #fff;
}

select.form-control:not([size]):not([multiple]) {
  height: auto;
}
